.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .chart-card {
    margin-bottom: 16px;
  }
  
  .chart-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .tabs-list {
    display: flex;
    gap: 12px;
    margin-bottom: 16px;
  }
  
  .tab-trigger {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: 600;
    color: #4a5568;
    background-color: #edf2f7;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    transition: background-color 0.3s;
  }
  
  .tab-trigger[data-state="active"] {
    background-color: #2563eb;
    color: white;
  }
  
  .dropdown-container {
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
  }
  
  .dropdown-trigger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 12px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
    font-size: 16px;
    cursor: pointer;
  }
  
  .dropdown-icon {
    width: 16px;
    height: 16px;
    margin-left: 8px;
  }
  
  .select-content {
    background-color: white;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    margin-top: 4px;
  }
  